<template>
	<div>
		<div class="project">
			<ul>
				<p class="hd">快捷导航</p>
				<li @click="$router.push('/appDownload')">易视云App(H5+Native混合App)</li>
				<li @click="$router.push('/Home')">卡租购</li>
				<li @click="dialogTip">青绿书架</li>
				<li><a href="https://segmentfault.com/u/jiang_rong">技术博客主页</a></li>
			</ul>
		</div>
		<my-dialog :config="dialogOption" ref="dialogTips"></my-dialog>
	</div>
</template>
<script>

import myDialog from "@/common/Dialog"
export default {
	data() {
		return {
			dialogOption: {
				content: "提示",
				subcontent: "打开微信,在发现页面点击小程序搜索'青绿书架'"
			}
		}
	},
	components: {
		myDialog
	},
	methods: {
		dialogTip() {
			this.$refs.dialogTips.show();
		}
	},
	computed: {

	},
	activated: function() {

	},
	mounted: function() {}
}

</script>
<style scoped>
.project ul {
	width: 90%;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.project p.hd{
	color: #3e78c4;
	font-size: 20px;
	padding: 20px 10px;
}
.project li {
	line-height: 0.5rem;
	padding: 0rem 0.2rem 0rem 0.1rem;
	border-bottom: 1px dashed #ccc;
	position: relative;
	font-size: 0.16rem;
}

.project li:after {
	content: "";
	position: absolute;
	top: 0rem;
	bottom: 0rem;
	margin: auto;
	right: 0.1rem;
	width: 0.1rem;
	height: 0.1rem;
	border-top: 0.01rem #bbb solid;
	border-right: 0.01rem #bbb solid;
	transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
}

.project li>a {
	display: block;
	margin-right: -0.2rem;
}

</style>



