<!-- 可爱风格的对话提示弹框组件 -->
<template>
    <div>
        <a-mask v-if="settings.showMask" v-show="isShow"></a-mask>
        <transition>
            <div class="dialog" v-show="isShow" @touchmove.prevent>
                <div class="dialog-center">
                    <div class="dialog-title">{{settings.content}}</div>
                    <div class="dialog-subcontent" v-if="settings.subcontent">{{settings.subcontent}}</div>
                </div>
                <div class="dialog-botton" @click="sure">{{settings.sureBtnText}}</div>
            </div>
        </transition>
    </div>
</template>
<script>
import aMask from "./Mask"
export default {
    name: 'mydialog',
    props: {
        config: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    data() {
        return {
            isShow: false
        }
    },
    computed: {
        // 使用config传入组件配置对象
        // settings是将默认设置和用户设置得到的最终设置值，将默认设置与用户配置项对象合并得到
        settings: function() {
            return Object.assign({
                showMask: true, //是否显示蒙版
                content: "", //必填,主体内容(样式加粗大字体)
                subcontent: "", //选填,解释说明的一些文字不显眼文字，无则不设置
                sureBtnText: "确定",
                showimg: false, //是否显示图片
            }, this.config)
        }
    },
    components: {
        aMask
    },
    methods: {
        show: function() {
            this.isShow = true;
        },
        //cancel做取消当前弹窗功能
        cancel: function() {
            this.isShow = false;
            this.$emit("close");
        },
        //点击确定的时候向父组件发射事件函数
        sure: function() {
            this.isShow = false;
            this.$emit("sure");
        }
    }

}

</script>
<style scoped>
.dialog {
    z-index: 6;
    background: rgba(255, 255, 255, 1);
    position: fixed;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    font-size: 32px;
    width: 287px;
    min-height: 172px;
    padding: 0 29px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    text-align: center;

}

.dialog-center {
    padding: 16px 0px 10px;
    box-sizing: border-box;
   line-height: 24px;
}

.dialog-title {
    font-size: 16px;
    font-weight: 500;
    color: rgba(51, 51, 51, 1);
    padding-bottom: 12px;
}

.dialog-subcontent {
    font-size: 17px;
    font-weight: 500;
    color: rgba(102, 102, 102, 1);
}

.dialog-botton {
    line-height: 40px;
    border-radius: 20px;
    font-size: 17px;
    font-weight: 500;
    display: inline-block;
    min-width: 90px;
    padding: 0px 8px;
    box-sizing: border-box;
    background-image: linear-gradient(to right, #3e78c4, #3e78c4);
    border-radius:16px;
    color: #fff;
}

</style>



