<template>
	<transition name="fade">
		<div class="mask" @click="handlerClick"  @touchmove.prevent></div>
	</transition>
</template>
<script>
export default {
	name: 'aMask',
	data() {
		return {
			timegap:false
		}
	},
	methods:{
		handlerClick: function() {
			// 使用函数节流避免多次点击，400ms下一次操作才生效
		    if (this.timegap) {clearTimeout(this.timegap);}
		    let callNow = !this.timegap;
		    this.timegap = setTimeout(() => {
		        this.timegap = null;
		    }, 1000)
		    if(callNow){
				// 点击蒙板触发父组件的clickMask自定义事件
				this.$emit('clickMask')	
		    }			    
		}
	}

}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mask {
	position: fixed;
	left: 0px;
	top: 0px;
	right: 0px;
	bottom: 0px;
	background: rgba(0, 0, 0, 0.5);
	z-index: 5;
}
.fade-enter,.fade-leave-to{
	opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
	transition: all 0.5s;
}

</style>



